"use client"

import { useState } from "react"
import Link from "next/link"
import Image from "next/image"
import Header from "../components/Header"
import Footer from "../components/Footer"
import { ArrowLeft, ExternalLink, X } from "lucide-react"

export default function Portfolio() {
  const [selectedProject, setSelectedProject] = useState(null)

  const projects = [
    {
      title: "Portfolio design",
      description: "UI design - User research - webflow develop",
      image: "/assets/p1.png",
      details: {
        fullDescription: "A modern portfolio website with clean design and smooth animations. Built with responsive design principles and optimized for performance.",
        images: [
          "/assets/p1.png",
          "/assets/p1-detail1.png",
          "/assets/p1-detail2.png",
        ],
        tech: ["Next.js", "React", "Tailwind CSS", "Framer Motion"],
        role: "Full Stack Developer & Designer",
        duration: "3 weeks",
        link: "#",
      },
    },
    {
      title: "EcoSCha Card Game",
      description: "UI design - User research - webflow develop",
      image: "/assets/project2.jpg",
      details: {
        fullDescription: "Educational card game platform for environmental awareness. Features interactive gameplay and learning modules.",
        images: [
          "/assets/project2.jpg",
          "/assets/project2-detail1.jpg",
        ],
        tech: ["React", "Node.js", "MongoDB", "Socket.io"],
        role: "Lead Developer",
        duration: "2 months",
        link: "#",
      },
    },
    {
      title: "I design & top",
      description: "UI design - User research - webflow develop",
      image: "/assets/project3.jpg",
      details: {
        fullDescription: "Creative design showcase platform with modern UI/UX principles.",
        images: [
          "/assets/project3.jpg",
        ],
        tech: ["Figma", "Webflow", "JavaScript"],
        role: "UI/UX Designer",
        duration: "2 weeks",
        link: "#",
      },
    },
    {
      title: "Find Your True Style",
      description: "UI design - User research - webflow develop",
      image: "/assets/project4.jpg",
      details: {
        fullDescription: "Fashion discovery platform helping users find their personal style.",
        images: [
          "/assets/project4.jpg",
        ],
        tech: ["React", "TailwindCSS", "Firebase"],
        role: "Frontend Developer",
        duration: "4 weeks",
        link: "#",
      },
    },
  ]

  return (
    <>
      <main className="min-h-screen bg-background">
        <Header />

        {/* Content */}
        <section className="max-w-6xl mx-auto px-4 py-8 md:py-16">
          <Link href="/" className="inline-flex items-center gap-2 text-foreground font-bold mb-8 hover:text-accent transition">
            <ArrowLeft className="w-5 h-5" />
            Back to Home
          </Link>

          <div className="space-y-8 md:space-y-12">
            {/* Header */}
            <div className="flex items-center gap-3 md:gap-4">
              <h1 className="text-3xl md:text-5xl lg:text-6xl font-black">My Portfolio</h1>
              <Image
                src="/assets/el2.png"
                alt="Decoration"
                width={60}
                height={60}
                className="object-contain star-decoration w-12 h-12 md:w-16 md:h-16 lg:w-20 lg:h-20"
              />
            </div>

            {/* Projects Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 md:gap-12">
              {projects.map((project, index) => (
                <button
                  key={index}
                  onClick={() => setSelectedProject(project)}
                  className="group block text-left w-full"
                >
                  {/* Container dengan aspect ratio */}
                  <div className="relative w-full aspect-[4/3]">
                    {/* Background Frame */}
                    <div className="absolute inset-0">
                      <Image
                        src="/assets/portoframe.png"
                        alt="Frame"
                        fill
                        className="object-contain"
                        priority={index < 2}
                      />
                    </div>

                    {/* Content Layer - padding yang pas */}
                    <div className="absolute inset-0 px-[20%] pt-[9%] pb-[16%] flex flex-col gap-1.5">
                      {/* Image dengan border - 80% space */}
                      <div className="relative flex-[4] border-2 border-foreground bg-white overflow-hidden">
                        <Image
                          src={project.image}
                          alt={project.title}
                          fill
                          className="object-cover group-hover:scale-105 transition-transform duration-300"
                        />
                      </div>
                      
                      {/* Info - 20% space */}
                      <div className="flex-[1] flex flex-col justify-center">
                        <div className="flex items-center justify-between gap-1 mb-0.5">
                          <h3 className="text-[11px] md:text-xs lg:text-sm font-bold group-hover:text-accent transition leading-none truncate">
                            {project.title}
                          </h3>
                          <ExternalLink className="w-3 h-3 md:w-3.5 md:h-3.5 flex-shrink-0 group-hover:text-accent transition" />
                        </div>
                        <p className="text-[8px] md:text-[9px] lg:text-[10px] text-muted-foreground leading-tight line-clamp-1">
                          {project.description}
                        </p>
                      </div>
                    </div>
                  </div>
                </button>
              ))}
            </div>
          </div>
        </section>

        {/* Floating decorations */}
        <div className="hidden md:block fixed top-32 right-12 text-4xl star-decoration opacity-70">âœ¨</div>
        <div className="hidden md:block fixed bottom-20 left-12 text-3xl star-decoration opacity-70">ðŸ’«</div>
      </main>

{/* Modal Popup */}
      {selectedProject && (
        <div 
          className="fixed inset-0 z-[100] flex items-center justify-center p-4 bg-black/50 backdrop-blur-sm"
          onClick={() => setSelectedProject(null)}
        >
          <div 
            className="bg-background border-4 border-foreground rounded-lg max-w-4xl w-full max-h-[90vh] overflow-hidden sketch-box flex flex-col"
            onClick={(e) => e.stopPropagation()}
          >
            {/* Header - Fixed at top */}
            <div className="bg-background border-b-4 border-foreground p-6 flex items-center justify-between flex-shrink-0">
              <h2 className="text-2xl md:text-3xl font-black">{selectedProject.title}</h2>
              <button
                onClick={() => setSelectedProject(null)}
                className="p-2 hover:bg-accent/10 rounded-lg transition"
              >
                <X className="w-6 h-6" />
              </button>
            </div>

            {/* Scrollable Content */}
            <div className="overflow-y-auto flex-1 p-6 space-y-6">
              {/* Images Gallery */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                {selectedProject.details.images.map((img, idx) => (
                  <div key={idx} className="relative aspect-video border-2 border-foreground rounded-lg overflow-hidden">
                    <Image
                      src={img}
                      alt={`${selectedProject.title} ${idx + 1}`}
                      fill
                      className="object-cover"
                    />
                  </div>
                ))}
              </div>

              {/* Description */}
              <div className="sketch-box p-4 bg-card">
                <h3 className="font-bold text-lg mb-2">About Project</h3>
                <p className="text-muted-foreground">{selectedProject.details.fullDescription}</p>
              </div>

              {/* Project Info */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="sketch-box p-4 bg-card">
                  <h3 className="font-bold mb-2">Role</h3>
                  <p className="text-sm text-muted-foreground">{selectedProject.details.role}</p>
                </div>
                <div className="sketch-box p-4 bg-card">
                  <h3 className="font-bold mb-2">Duration</h3>
                  <p className="text-sm text-muted-foreground">{selectedProject.details.duration}</p>
                </div>
              </div>

              {/* Tech Stack */}
              <div className="sketch-box p-4 bg-card">
                <h3 className="font-bold mb-3">Technologies Used</h3>
                <div className="flex flex-wrap gap-2">
                  {selectedProject.details.tech.map((tech, idx) => (
                    <span
                      key={idx}
                      className="px-3 py-1 bg-accent/20 border-2 border-foreground rounded-full text-sm font-bold"
                    >
                      {tech}
                    </span>
                  ))}
                </div>
              </div>

              {/* View Project Link */}
              {selectedProject.details.link !== "#" && (
                <Link
                  href={selectedProject.details.link}
                  target="_blank"
                  className="flex items-center justify-center gap-2 w-full py-3 bg-accent text-background font-bold rounded-lg hover:scale-105 transition-transform"
                >
                  View Live Project
                  <ExternalLink className="w-4 h-4" />
                </Link>
              )}
            </div>
          </div>
        </div>
      )}

      <Footer />
    </>
  )
}